
body {
    background-color: #1c1c2f;
}
.action {
    margin: 10px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 20px;
}
.action .preview {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    width: 40%;
}
.action .setting {
    padding: 20px;
    width: 60%;
}
.el {
    width: 30px;
    background: #d61e77;
    height: 30px;
    margin: 0 auto;
}
.parameter {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.param-name {
    padding: 20px;
    color: #fff;
    width: 10%;
}
.param-value {
    padding: 20px;
    color: #fff;
    width: 60%;
}
.referrer {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.referrer-name {
    padding: 20px;
    color: #fff;
    width: 100%;
}




